import { Routes } from '@angular/router';

import { DashboardComponent } from './dashboard/dashboard.component';

import { SearchUserComponent } from './search-user/search-user.component';
import { AddUserComponent } from './add-user/add-user.component';

import { SearchListingComponent } from './search-listing/search-listing.component';
import { AddListingComponent } from './add-listing/add-listing.component';

import { SearchBookingComponent } from './search-booking/search-booking.component';
import { CreateQuoteComponent } from "./create-quote/create-quote.component";

export const AppRoutes: Routes = [
    {
        path: '',
        redirectTo: '/dashboard',
        pathMatch: 'full',
    },
    {
        path: 'dashboard',
        component: DashboardComponent
    },
    {
        path: 'user/search-user',
        component: SearchUserComponent
    },
    { 
        path: 'user/add-user',
        component: AddUserComponent
    },
    {
        path: 'listing/search-listing',
        component: SearchListingComponent
    },
    {
        path: 'listing/add-listing',
        component: AddListingComponent
    },
    {
        path: 'booking/search-booking',
        component: SearchBookingComponent
    },
    {
        path: 'booking/create-quote',
        component: CreateQuoteComponent
    }

]



